<template>
    <div class="main-center main-center-2">
        <div class="parent-center">
            <p class="mod-tit mt25">
                <span class="left-bor"></span>
                <span class="mod-txt">家长信息</span>
            </p>
            <div class="user-box parent-box common-bor">
                <img :src="accoutInfo.person_info_data.head_portrait_url" alt="">
                <div class="info">
                    <p class="user-name">{{accoutInfo.person_info_data.nick_name|| accoutInfo.person_info_data.phone}}</p>
                    <span class="phone">账户ID：{{accoutInfo.code}}</span>
                </div>
            </div>
            <p class="mod-tit mt25">
                <span class="left-bor"></span>
                <span class="mod-txt">我的孩子</span>
            </p>
            <div class="child-list">
                <div class="common-bor child-box"
                     :key="item.student_id"
                     v-for="item in studentList">
                    <div class="user-box">
                        <img :src="item.head_portrait_url" alt="">
                        <div class="info">
                            <p>{{item.name}}</p>
                            <span>学号：{{item.code}}</span>
                        </div>
                        <div class="count-box">
                            <div class="count-list">
                                <p class="num">{{item.arrive_count}}<span class="unit">次</span></p>
                                <p class="count-tit">到课次数</p>
                            </div>
                            <div class="line"></div>
                            <div class="count-list">
                                <p class="num">{{item.submit_task_count}}<span class="unit">个</span></p>
                                <p class="count-tit">已参与学习</p>
                            </div>
                            <div class="line"></div>
                             <div class="count-list">
                                <p class="num">{{item.pay_transaction_record_count}}<span class="unit">次</span></p>
                                <p class="count-tit">已学课次</p>
                            </div>
                            <div class="line"></div>
                             <div class="count-list">
                                <p class="num">{{item.time_total}}<span class="unit">{{item.time_name}}</span></p>
                                <p class="count-tit">累计听读</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapActions,mapGetters} from 'vuex'
    export default {
        data: () => ({
            submitAble:false,
            studentList:[]
        }),
        methods:{
            ...mapActions('child',['getLearningUserStudentSimpleHasComprehensiveStatisticsListActions']),
            getStuCenterList(){
                this.getLearningUserStudentSimpleHasComprehensiveStatisticsListActions().then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                       this.studentList = data;
                       this.studentList.forEach(item=>{
                           item.book_completion_long_sum = Number(item.book_completion_long_sum);
                           if(item.book_completion_long_sum==0){
                               item.time_total = item.book_completion_long_sum;
                               item.time_name = '';
                            }else if(item.book_completion_long_sum>0&&item.book_completion_long_sum<=60){
                                item.time_name = '秒';
                                item.time_total = item.book_completion_long_sum;
                            }else if(item.book_completion_long_sum>60&&item.book_completion_long_sum<=3600) {
                                //小于1小时 显示时间向下取整
                                item.time_total = Math.floor((item.book_completion_long_sum/60));
                                item.time_name = '分钟';
                            }else {
                                // 大于1小时 向下保留1位小数
                                item.time_total = parseInt((item.book_completion_long_sum/3600)*10)/10;
                                item.time_name = '小时';
                            }
                       })
                    }
                })
            },
        },
        watch:{

        },
        mounted(){
            this.getStuCenterList()
        },
        computed: {
            ...mapGetters('account',{
                accoutInfo: 'info'
            })
        },
        components: {},
        created() {}
    }
</script>

<style lang="postcss" scoped>
    .op-a{
        font-size:1rem;
        color:rgba(89,155,255,1);
        margin-left: 2.33rem;
        &:hover{
            text-decoration: underline;
        }
    }
</style>
